

// Small devices (landscape phones, less than 768px)
@media (max-width: 767.98px) {
	.col-length-3:nth-last-child(-n+2),
	.col-length-6:nth-last-child(-n+5) {
		margin-top: 30px;
	}
	.site-header {
		padding: 20px 0;
	}
	.navbar-brand {
	  	left: 15px;
	}
	.navbar-collapse {
	  	padding: 0 15px 15px;
	}
	.navbar-nav {
		max-height: 300px;
		overflow: auto;
		> li > a {
		  	&::before {
		  		left: 0;
		  	}
		}
		.submenu {
		  	border-left: 2px solid $primary-color;
		  	border-top: 0 none;
		  	display: none;
		  	padding: 10px 10px 10px 15px;
		  	position: relative;
		  	@include transition(all 0s ease 0s);
		  	@include transform(scaleY(1));
		  	> li > a {
		  	  	margin-bottom: 15px;
		  	}
		}
	}
	.header-sticky {
		.navbar-nav > li > a {
	    	padding: 10px 0;
	    }
	}
	.main-banner-2 {
		.row {
		    height: 80vh;
		    padding-top: 40px;
		}
		h2 {
		    font-size: 45px;
		}
	}
	.page-header {
	  	padding: 150px 0 120px;
	  	h1 {
	  		font-size: 28px;
	  	}
	}
	.blog-post figcaption {
		padding: 15px;
		width: 100%;
	}
	#widget-area {
		margin-top: 100px;
	}
	#contact-form {
		margin-top: 50px;
	}
	.watermark-title {
		display: none;
	}
	.video-button {
	    position: static;
	    margin: 40px;
	    @include transform(none);
	    .play-icon {
	    	@include transform(none);
	    }
	}
}

// Extra small devices (portrait phones, less than 576px)
@media (max-width: 575.98px) {
	.buttons-group .btn-bottom-border {
		padding: 16px 20px 18px;
	}
	.banner-content {
	  	height: 380px;
	  	h3 {
	  		font-size: 25px;
	  	}
	  	p {
	  		font-size: 10px;
	  	}
	  	.caption1 {
	  	  	left: calc(50% - 52px);
	  	  	top: 245px;
	  	  	p {
	  	  	  	margin: 9px 0 0 -10px;
	  	  	  	text-align: left;
	  	  	}
	  	}
	  	.caption2 {
	  	  	right: calc(50% - 25px);
	  	  	top: calc(50% + 80px);
	  	  	p {
	  	  		bottom: 13px;
	  	  		right: -62px;
	  	  		top: -5px;
	  	  	}
	  	}
	}
	.section-heading h2 {
	  	font-size: 28px;
	}
	.trainer-bio > .col-12:last-child,
	.col-length-4:nth-last-child(-n+2),
	.col-length-3:last-child,
	.col-length-2:last-child {
		margin-top: 30px;
	}
	.footer-nav li a {
		padding: 0 5px;
	}
}

// Small devices (landscape phones, 576px and up)
@media (min-width: 576px) and (max-width: 767.98px) {

}

// Medium devices (tablets, 768px and up)
@media (min-width: 768px) and (max-width: 991.98px) {
	// generated by js
	.col-length-3:last-child,
	.col-length-6:nth-last-child(-n+4) {
		margin-top: 30px;
	}
	.col-length-4:nth-child(2),
	.col-length-4:nth-child(3) {
	  margin-bottom: 30px;
	}
	.site-header {
		padding: 20px 0;
	}
	.primary-nav {
	  	padding-bottom: 10px;
	}
	.navbar-nav {
		max-height: 300px;
		overflow: auto;
		> li > a {
	  		padding: 10px 0;
		  	&::before {
		  		left: 0;
		  	}
		}
		.submenu {
		  	border-left: 2px solid $primary-color;
		  	border-top: 0 none;
		  	display: none;
		  	padding: 10px 10px 10px 15px;
		  	position: relative;
		  	@include transition(all 0s ease 0s);
		  	@include transform(scaleY(1));
		  	> li > a {
		  	  	margin-bottom: 15px;
		  	}
		}
	}
	.header-sticky {
		.navbar-nav > li > a {
	    	padding: 10px 0;
	    }
	}
	.banner-content {
		height: 60vh;
	}
	.banner-content {
		h3 {
			font-size: 60px;
		}
		.caption1 {
			@include calc('left', '50% - 110px');
			@include calc('top', '50% + 40px');
		}
		.caption2 {
			@include calc('right', '50% - 115px');
			@include calc('top', '50% + 89px');
		}
	}
	.main-banner-2 .row {
	    height: 80vh;
	    padding-top: 40px;
	}
	.trainer-slider {
		.slick-arrow {
			top: 50%;
			@include transform(translateY(-50%));
		}
		.slick-prev {
		  	left: 0;
		}
		.slick-next {
		  	left: auto;
		  	right: 0;
		}
	}
	.trainer-bio {
		.buttons-group {
			margin-top: 50px;
		}
		.btn-bottom-border {
			padding: 8px 28px 10px;
		}
	}
	#bmi-calc-form {
	    margin-top: 50px;
	}
	.blog-post figcaption {
	  	width: 70%;
	}
	#widget-area {
		margin-top: 100px;
	}
	.watermark-title {
		font-size: 200px;
	}
	#about .watermark-title {
	    left: -80px;
	}
	#our-services .watermark-title {
	    right: -80px;
	}
	#our-trainers .watermark-title {
	    left: -180px;
	}
	#our-pricing .watermark-title {
	    right: -140px;
	}
}

// Large devices (desktops, 992px and up)
@media (min-width: 992px) and (max-width: 1199.98px) {
	.banner-content {
		h3 {
			font-size: 80px;
		}
		.caption1 {
			@include calc('left', '50% - 145px');
			@include calc('top', '50% + 35px');
		}
		.caption2 {
			@include calc('right', '50% - 150px');
			@include calc('top', '50% + 100px');
		}
	}
	.trainer-bio .btn-bttom-border {
	  	padding: 13px 35px 15px;
	}
	.watermark-title {
		font-size: 200px;
	}
	#about .watermark-title {
	    left: -80px;
	}
	#our-services .watermark-title {
	    right: -80px;
	}
	#our-trainers .watermark-title {
	    left: -180px;
	}
	#our-pricing .watermark-title {
	    right: -140px;
	}
}

// Extra large devices (large desktops, 1200px and up)
@media (min-width: 1200px) {
	.col-length-6:nth-last-child(-n+3) {
		margin-top: 30px;
	}
}

// Extra large devices (large desktops, 1200px and up)
@media (min-width: 1201px) and (max-width: 1599.98px) {
	.watermark-title {
		font-size: 300px;
	}
	#about .watermark-title {
	    left: -130px;
	}
	#our-services .watermark-title {
	    right: -135px;
	}
	#our-trainers .watermark-title {
	    left: -240px;
	}
	#our-pricing .watermark-title {
	    right: -210px;
	}
}